<template>
   <div class="container">
    <h1 class="section-title">
        <span class="section-icon">üìä</span>
        NMEA CEP Í≥ÑÏÇ∞Í∏∞
    </h1>
    
    <div class="calculation-mode-section">
      <div class="mode-selector">
        <label class="mode-label">
          <input type="radio" v-model="calculationMode" value="stationary" />
          Ï†ïÏßÄÏÉÅÌÉú CEP Í≥ÑÏÇ∞
        </label>
        <label class="mode-label">
          <input type="radio" v-model="calculationMode" value="moving" />
          Ïù¥ÎèôÏÉÅÌÉú CEP Í≥ÑÏÇ∞
        </label>
      </div>
    </div>

    <div class="section-card">      
      <div class="file-upload-section">
        <template v-if="calculationMode === 'stationary'">
          <div class="file-upload-group">
            <label class="file-upload-button">
              <span class="file-upload-text">Ï∏°Ï†ï GPS ÌååÏùº ÏÑ†ÌÉù</span>
              <input type="file" accept=".txt,.log" @change="handleTestFileUpload" class="file-input" />
            </label>
            <span v-if="testFileName" class="selected-filename"> {{ testFileName }}</span>
          </div>
        </template>
        <template v-else>
          <div class="file-upload-group">
            <label class="file-upload-button">
              <span class="file-upload-text">Î†àÌçºÎü∞Ïä§ GPS ÌååÏùº ÏÑ†ÌÉù</span>
              <input type="file" accept=".txt,.log" @change="handleReferenceFileUpload" class="file-input" />
            </label>
            <span v-if="referenceFileName" class="selected-filename"> {{ referenceFileName }}</span>
          </div>
          <div class="file-upload-group">
            <label class="file-upload-button">
              <span class="file-upload-text">Ï∏°Ï†ï GPS ÌååÏùº ÏÑ†ÌÉù</span>
              <input type="file" accept=".txt,.log" @change="handleTestFileUpload" class="file-input" />
            </label>
            <span v-if="testFileName" class="selected-filename"> {{ testFileName }}</span>
          </div>
        </template>
      </div>
    </div>

    <h2 class="font-semibold">üìä ÏúÑÏπò Îç∞Ïù¥ÌÑ∞</h2>
    <transition name="fade">      
      <div v-if="points.length" class="section-card">
        <div v-if="calculationMode === 'stationary'" class="center-point-info">
          Ï§ëÏã¨Ï†ê: ÏúÑÎèÑ {{ center.lat.toFixed(6) }}, Í≤ΩÎèÑ {{ center.lon.toFixed(6) }}
        </div>
        <div class="scrollable-container">
          <table class="data-table">
            <thead>
              <tr class="header-row">
                <th class="header-cell">#</th>
                <th class="header-cell">ÏãúÍ∞Ñ</th>
                <template v-if="calculationMode === 'moving'">
                  <th class="header-cell">Î†àÌçºÎü∞Ïä§ ÏúÑÎèÑ</th>
                  <th class="header-cell">Î†àÌçºÎü∞Ïä§ Í≤ΩÎèÑ</th>
                </template>
                <th class="header-cell">Ï∏°Ï†ï ÏúÑÎèÑ</th>
                <th class="header-cell">Ï∏°Ï†ï Í≤ΩÎèÑ</th>
                <th class="header-cell">Í±∞Î¶¨ (m)</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(p, i) in points" :key="i" class="data-row">
                <td class="data-cell">{{ i + 1 }}</td>
                <td class="data-cell">{{ formatTime(p.test.time) }}</td>
                <template v-if="calculationMode === 'moving'">
                  <td class="data-cell">{{ p.reference.lat.toFixed(6) }}</td>
                  <td class="data-cell">{{ p.reference.lon.toFixed(6) }}</td>
                </template>
                <td class="data-cell">{{ p.test.lat.toFixed(6) }}</td>
                <td class="data-cell">{{ p.test.lon.toFixed(6) }}</td>
                <td class="data-cell">{{ p.dist3d.toFixed(3) }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div v-else class="empty-state">
        ÌååÏùºÏùÑ ÏóÖÎ°úÎìúÌïòÎ©¥ Î∂ÑÏÑù Í≤∞Í≥ºÍ∞Ä Ïó¨Í∏∞Ïóê ÌëúÏãúÎê©ÎãàÎã§.
      </div>
    </transition>
  
    <h2 class="mt-6 font-semibold">üìà Í∑∏ÎûòÌîÑ </h2>
    <transition name="fade">  
      <div v-if="points.length">           
        <canvas ref="chartRef"></canvas>        
      </div>
      <div v-else class="empty-state">
        ÌååÏùºÏùÑ ÏóÖÎ°úÎìúÌïòÎ©¥ Î∂ÑÏÑù Í≤∞Í≥ºÍ∞Ä Ïó¨Í∏∞Ïóê ÌëúÏãúÎê©ÎãàÎã§.
      </div>
    </transition>

    <h2 class="mt-6 font-semibold">üìå ÌÜµÍ≥Ñ</h2>
    <transition name="fade">  
      <div v-if="points.length">
        <table class="stats-table">
          <thead >
            <tr>
              <th>ITEMS</th> 
              <th>CEP50</th>            
              <th>CEP68</th>
              <th>CEP95</th>
              <th>CEP99</th>
              <th>ÏµúÏÜå Í±∞Î¶¨</th>            
              <th>ÏµúÎåÄ Í±∞Î¶¨</th>
              <th>ÌèâÍ∑† Í±∞Î¶¨</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>PACC_3D</td>
              <td>{{ accuracyTypes.pacc_3d.cep50.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.cep68.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.cep95.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.cep99.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.min.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.max.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_3d.avg.toFixed(3) }} m</td>
            </tr>
            <tr>
              <td>PACC_H</td>
              <td>{{ accuracyTypes.pacc_h.cep50.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.cep68.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.cep95.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.cep99.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.min.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.max.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_h.avg.toFixed(3) }} m</td>
            </tr><tr>
              <td>PACC_V</td>
              <td>{{ accuracyTypes.pacc_v.cep50.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.cep68.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.cep95.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.cep99.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.min.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.max.toFixed(3) }} m</td>
              <td>{{ accuracyTypes.pacc_v.avg.toFixed(3) }} m</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-else class="empty-state">
        ÌååÏùºÏùÑ ÏóÖÎ°úÎìúÌïòÎ©¥ Î∂ÑÏÑù Í≤∞Í≥ºÍ∞Ä Ïó¨Í∏∞Ïóê ÌëúÏãúÎê©ÎãàÎã§.
      </div>
    </transition>
  </div>
</template>
  
<script setup>
  import { ref, nextTick, watch } from 'vue'
  import { Chart, ScatterController, LinearScale, PointElement, Title, Tooltip } from 'chart.js'
  
  Chart.register(ScatterController, LinearScale, PointElement, Title, Tooltip)
  
  const points = ref([])
  const referencePoints = ref([])
  const testPoints = ref([])
  const accuracyTypes = ref({
    pacc_3d: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 },
    pacc_h: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 },
    pacc_v: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 }
  })
  const referenceFileName = ref('')
  const testFileName = ref('')
  const center = ref({ lat: 0, lon: 0 })
  const chartRef = ref()
  let chartInstance = null
  const calculationMode = ref('stationary') // 'stationary' or 'moving'

  // Í≥ÑÏÇ∞ Î™®Îìú Î≥ÄÍ≤Ω Ïãú Îç∞Ïù¥ÌÑ∞ Ï¥àÍ∏∞Ìôî
  watch(calculationMode, () => {
    // Î™®Îì† Îç∞Ïù¥ÌÑ∞ Ï¥àÍ∏∞Ìôî
    points.value = []
    referencePoints.value = []
    testPoints.value = []
    referenceFileName.value = ''
    testFileName.value = ''
    accuracyTypes.value = {
      pacc_3d: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 },
      pacc_h: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 },
      pacc_v: { cep50: 0, cep68: 0, cep95: 0, cep99: 0, min: 0, max: 0, avg: 0 }
    }
    center.value = { lat: 0, lon: 0, alt: 0 }
    
    // Ï∞®Ìä∏ Ï¥àÍ∏∞Ìôî
    if (chartInstance) {
      chartInstance.destroy()
      chartInstance = null
    }
  })

  function handleReferenceFileUpload(event) {
    const file = event.target.files[0]
    if (!file) return
    referenceFileName.value = file.name
    const reader = new FileReader()
    reader.onload = () => {
      const cleanedText = reader.result.replace(/\0/g, '')
      referencePoints.value = parseNmeaData(cleanedText)
      if (testPoints.value.length > 0) {
        calculateMovingCep()
      }
    }
    reader.readAsText(file)
  }

  function handleTestFileUpload(event) {
    const file = event.target.files[0]
    if (!file) return
    testFileName.value = file.name
    const reader = new FileReader()
    reader.onload = () => {
      const cleanedText = reader.result.replace(/\0/g, '')
      testPoints.value = parseNmeaData(cleanedText)
      
      if (calculationMode.value === 'stationary') {
        // Ï†ïÏßÄÏÉÅÌÉú Í≥ÑÏÇ∞ Ïãú Î∞îÎ°ú CEP Í≥ÑÏÇ∞
        const avgLat = testPoints.value.reduce((s, p) => s + p.lat, 0) / testPoints.value.length
        const avgLon = testPoints.value.reduce((s, p) => s + p.lon, 0) / testPoints.value.length
        const avgAlt = testPoints.value.reduce((s, p) => s + p.alt, 0) / testPoints.value.length
        center.value = { lat: avgLat, lon: avgLon, alt: avgAlt }
        calculateStationaryCep()
      } else if (referencePoints.value.length > 0) {
        // Ïù¥ÎèôÏÉÅÌÉú Í≥ÑÏÇ∞ Ïãú Î†àÌçºÎü∞Ïä§ Îç∞Ïù¥ÌÑ∞Í∞Ä ÏûàÏúºÎ©¥ CEP Í≥ÑÏÇ∞
        calculateMovingCep()
      }
    }
    reader.readAsText(file)
  }

  function parseNmeaData(text) {
    const lines = text.split(/\r?\n/)
    const points = []
    let alt = 0
    let time = 0

    for (const line of lines) {
      let latRaw = '', latDir = '', lonRaw = '', lonDir = ''

      if (line.startsWith('$GNGGA') || line.startsWith('$GPGGA')) {
        const parts = line.split(',')
        latRaw = parts[2]
        latDir = parts[3]
        lonRaw = parts[4]
        lonDir = parts[5]
        alt = !isNaN(parseFloat(parts[9])) ? parseFloat(parts[9]) : 0
        time = parseTime(parts[1])
      } else if (line.startsWith('$GNRMC') || line.startsWith('$GPRMC')) {
        const parts = line.split(',')
        latRaw = parts[3]
        latDir = parts[4]
        lonRaw = parts[5]
        lonDir = parts[6]
        time = parseTime(parts[1])
      }

      const lat = convertToDecimal(latRaw, latDir)
      const lon = convertToDecimal(lonRaw, lonDir)

      if (!isNaN(lat) && !isNaN(lon)) {
        points.push({ lat, lon, alt, time })
      }
    }

    return points
  }

  function parseTime(timeStr) {
    if (!timeStr) return 0
    const hours = parseInt(timeStr.substring(0, 2))
    const minutes = parseInt(timeStr.substring(2, 4))
    const seconds = parseFloat(timeStr.substring(4))
    return hours * 3600 + minutes * 60 + seconds
  }

  // function calculateCep() {
  //   if (referencePoints.value.length === 0 || testPoints.value.length === 0) return

  //   if (calculationMode.value === 'stationary') {
  //     calculateStationaryCep()
  //   } else {
  //     calculateMovingCep()
  //   }
  // }
  
  function calculateStationaryCep() {
    // Ï†ïÏßÄÏÉÅÌÉú CEP Í≥ÑÏÇ∞ (Îã®Ïùº ÌååÏùº)
    const calculatedPoints = testPoints.value.map(p => ({
      reference: { lat: center.value.lat, lon: center.value.lon, alt: center.value.alt, time: p.time },
      test: { lat: p.lat, lon: p.lon, alt: p.alt, time: p.time },
      dist3d: calculate3DDistance(center.value, p),
      distH: calculateHDistance(center.value, p),
      distV: calculateVDistance(center.value, p)
    }))

    points.value = calculatedPoints

    // CEP Í≥ÑÏÇ∞
    accuracyTypes.value.pacc_3d = calculateAccuracyStats(calculatedPoints.map(p => p.dist3d))
    accuracyTypes.value.pacc_h = calculateAccuracyStats(calculatedPoints.map(p => p.distH))
    accuracyTypes.value.pacc_v = calculateAccuracyStats(calculatedPoints.map(p => p.distV))

    if (calculatedPoints.length > 0) {
      nextTick(() => drawChart())
    }
  }

  function calculateMovingCep() {
    // Ïù¥Îèô ÏÉÅÌÉú ÌåêÎã®ÏùÑ ÏúÑÌïú Ìè¨Ïù∏Ìä∏ Ïåç Ï∞æÍ∏∞
    const matchedPoints = []
    const timeTolerance = 1 // 1Ï¥à Ïù¥ÎÇ¥Ïùò Îç∞Ïù¥ÌÑ∞Î•º Îß§Ïπ≠

    for (const refPoint of referencePoints.value) {
      const closestTestPoint = testPoints.value.reduce((closest, testPoint) => {
        const timeDiff = Math.abs(refPoint.time - testPoint.time)
        if (timeDiff < timeTolerance && (!closest || timeDiff < Math.abs(refPoint.time - closest.time))) {
          return testPoint
        }
        return closest
      }, null)

      if (closestTestPoint) {
        matchedPoints.push({
          reference: refPoint,
          test: closestTestPoint,
          dist3d: calculate3DDistance(refPoint, closestTestPoint),
          distH: calculateHDistance(refPoint, closestTestPoint),
          distV: calculateVDistance(refPoint, closestTestPoint)
        })
      }
    }

    points.value = matchedPoints

    // CEP Í≥ÑÏÇ∞
    accuracyTypes.value.pacc_3d = calculateAccuracyStats(matchedPoints.map(p => p.dist3d))
    accuracyTypes.value.pacc_h = calculateAccuracyStats(matchedPoints.map(p => p.distH))
    accuracyTypes.value.pacc_v = calculateAccuracyStats(matchedPoints.map(p => p.distV))

    if (matchedPoints.length > 0) {
      nextTick(() => drawChart())
    }
  }
  
  function calculate3DDistance(p1, p2) {
    const h_dist = haversine(p1.lat, p1.lon, p2.lat, p2.lon)
    const v_dist = Math.abs(p2.alt - p1.alt)
    return Math.sqrt(h_dist * h_dist + v_dist * v_dist)
  }

  function calculateHDistance(p1, p2) {
    return haversine(p1.lat, p1.lon, p2.lat, p2.lon)
  }

  function calculateVDistance(p1, p2) {
    return Math.abs(p2.alt - p1.alt)
  }

  function calculateAccuracyStats(distances) {
    // Ïù¥ÏÉÅÏπò Ï†úÍ±∞ (ÌèâÍ∑†ÏóêÏÑú 3 ÌëúÏ§ÄÌé∏Ï∞® Ïù¥ÏÉÅ Î≤óÏñ¥ÎÇú Í∞í)
    const mean = distances.reduce((sum, val) => sum + val, 0) / distances.length
    const stdDev = Math.sqrt(distances.reduce((sum, val) => sum + Math.pow(val - mean, 2), 0) / distances.length)
    const filteredDistances = distances.filter(d => Math.abs(d - mean) <= 3 * stdDev)
    
    const sorted = filteredDistances.sort((a, b) => a - b)
    const len = sorted.length
    
    // Ï†ïÌôïÌïú Ïù∏Îç±Ïä§ Í≥ÑÏÇ∞
    const idx50 = Math.round(len * 0.50) - 1
    const idx68 = Math.round(len * 0.68) - 1
    const idx95 = Math.round(len * 0.95) - 1
    const idx99 = Math.round(len * 0.99) - 1
    
    return {
        cep50: sorted[idx50] || 0,
        cep68: sorted[idx68] || 0,
        cep95: sorted[idx95] || 0,
        cep99: sorted[idx99] || 0,
        min: sorted[0] || 0,
        max: sorted[sorted.length - 1] || 0,
        avg: sorted.reduce((sum, v) => sum + v, 0) / len || 0
    }
}

  function convertToDecimal(raw, dir) {
    const deg = parseFloat(raw.slice(0, dir === 'N' || dir === 'S' ? 2 : 3))
    const min = parseFloat(raw.slice(dir === 'N' || dir === 'S' ? 2 : 3))
    let dec = deg + min / 60
    if (dir === 'S' || dir === 'W') dec *= -1
    return dec
  }
  
  function haversine(lat1, lon1, lat2, lon2) {
    const R = 6371000
    const toRad = deg => deg * Math.PI / 180
    const dLat = toRad(lat2 - lat1)
    const dLon = toRad(lon2 - lon1)
    const a = Math.sin(dLat / 2) ** 2 +
              Math.cos(toRad(lat1)) * Math.cos(toRad(lat2)) *
              Math.sin(dLon / 2) ** 2
    return 2 * R * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
  }
  
  function formatTime(seconds) {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    const secs = Math.floor(seconds % 60)
    return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  function drawChart() {
    if (!chartRef.value) return
    if (chartInstance) chartInstance.destroy()

    const referenceData = points.value.map(p => ({
      x: haversine(center.value.lat, center.value.lon, center.value.lat, p.reference.lon) * (p.reference.lon > center.value.lon ? 1 : -1),
      y: haversine(center.value.lat, center.value.lon, p.reference.lat, center.value.lon) * (p.reference.lat > center.value.lat ? 1 : -1),
      lat: p.reference.lat.toFixed(6),
      lon: p.reference.lon.toFixed(6)
    }))

    const testData = points.value.map(p => ({
      x: haversine(center.value.lat, center.value.lon, center.value.lat, p.test.lon) * (p.test.lon > center.value.lon ? 1 : -1),
      y: haversine(center.value.lat, center.value.lon, p.test.lat, center.value.lon) * (p.test.lat > center.value.lat ? 1 : -1),
      lat: p.test.lat.toFixed(6),
      lon: p.test.lon.toFixed(6),
      dist3d: p.dist3d.toFixed(3)
    }))

    // Find max range for square aspect ratio
    const maxRange = Math.max(
      ...referenceData.map(p => Math.abs(p.x)),
      ...referenceData.map(p => Math.abs(p.y)),
      ...testData.map(p => Math.abs(p.x)),
      ...testData.map(p => Math.abs(p.y))
    ) * 1.2 // Add 20% padding

    chartInstance = new Chart(chartRef.value, {
      type: 'scatter',
      data: {
        datasets: [{
          label: calculationMode.value === 'stationary' ? 'Center Point' : 'Reference GPS',
          data: referenceData,
          backgroundColor: 'rgba(0, 0, 255, 0.6)',
          pointRadius: 4
        },
        {
          label: 'Test GPS',
          data: testData,
          backgroundColor: 'rgba(255, 0, 0, 0.6)',
          pointRadius: 4
        }]
      },
      options: {
        aspectRatio: 1,
        responsive: true,
        interaction: {
          mode: 'nearest',
          axis: 'xy',
          intersect: true
        },
        plugins: {
          tooltip: {
            enabled: true,
            mode: 'point',
            intersect: true,
            position: 'nearest',
            filter: function(tooltipItem) {
              // Í∞ÄÏû• Í∞ÄÍπåÏö¥ Ìè¨Ïù∏Ìä∏Îßå ÌëúÏãú
              return tooltipItem.datasetIndex === 0 || tooltipItem.datasetIndex === 1;
            },
            callbacks: {
              label: function(context) {
                const data = context.raw
                if (context.dataset.label === 'Test GPS') {
                  return `ÏúÑÎèÑ: ${data.lat}, Í≤ΩÎèÑ: ${data.lon}, Í±∞Î¶¨: ${data.dist3d}m`
                } else {
                  return `ÏúÑÎèÑ: ${data.lat}, Í≤ΩÎèÑ: ${data.lon}`
                }
              }
            }
          },
          title: { 
            display: true, 
            text: 'Position Scatter Plot with CEP Circles',
            padding: 20
          },
          legend: {
            display: true,
            position: 'bottom'
          },
          cepCircles: {
            radii: [
              {value: accuracyTypes.value.pacc_3d.cep50, label: 'CEP50'},
              {value: accuracyTypes.value.pacc_3d.cep68, label: 'CEP68'},
              {value: accuracyTypes.value.pacc_3d.cep95, label: 'CEP95'},
              {value: accuracyTypes.value.pacc_3d.cep99, label: 'CEP99'}
            ]
          }
        },
        scales: {
          x: {
            title: { display: true, text: 'East-West (meters)' },
            min: -maxRange,
            max: maxRange,
            grid: {
              color: 'rgba(0, 0, 0, 0.1)',
              drawTicks: true,
              drawOnChartArea: true
            },
            ticks: {
              stepSize: maxRange/5
            }
          },
          y: {
            title: { display: true, text: 'North-South (meters)' },
            min: -maxRange,
            max: maxRange,
            grid: {
              color: 'rgba(0, 0, 0, 0.1)',
              drawTicks: true,
              drawOnChartArea: true
            },
            ticks: {
              stepSize: maxRange/5
            }
          }
        }
      },
      plugins: [{
        id: 'cepCircles',
        beforeDatasetsDraw(chart, args, pluginOptions) {
          const { ctx, scales } = chart
          const centerX = scales.x.getPixelForValue(0)
          const centerY = scales.y.getPixelForValue(0)
          const radii = pluginOptions.radii || []
          const colors = ['red', 'blue', 'orange', 'green']

          ctx.save()
          radii.forEach((r, i) => {
            const radiusInPixels = Math.abs(scales.x.getPixelForValue(r.value) - centerX)
            
            ctx.beginPath()
            ctx.arc(centerX, centerY, radiusInPixels, 0, 2 * Math.PI)
            ctx.strokeStyle = colors[i].replace('0.2', '1.0')
            ctx.lineWidth = 2
            ctx.stroke()
            
            const angle = Math.PI / 4
            const labelX = centerX + (radiusInPixels * Math.cos(angle))
            const labelY = centerY - (radiusInPixels * Math.sin(angle))
            
            ctx.fillStyle = colors[i].replace('0.2', '1.0')
            ctx.font = '12px Arial'
            ctx.textAlign = 'left'
            ctx.fillText(`${r.label}: ${r.value.toFixed(2)}m`, labelX, labelY)
          })
          ctx.restore()
        }
      }]
    })
  }

</script>
  
